.TodoSearch{
    background-color: rgb(255, 255, 255);
    display: block;
    margin: 0 auto;
    text-align: center;
    height: 60px;
    font-size: 20px;
    width: calc(100% - 150px);
    border: 2px solid rgb(6, 9, 12);
    border-radius: 0px;
    font-family: 'Montserrat Alternates', sans-serif;
}
.TodoSearch::placeholder{
    color: rgba(0, 0, 0, 0.479);
    font-family: 'Montserrat Alternates', sans-serif;

}
.TodoSearch:focus{
    color: rgb(0, 0, 0);
    outline-color: rgb(5, 144, 144);
    box-shadow: inset 0px 0px 11px 4px rgba(5, 144, 144, 0.404);
}