.TodoItem{
    background-color: rgb(123, 255, 255);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}
.TodoItemP{
    margin: 24px 0 24px 24px;
    width: calc(100% - 110px);
    font-size: 20px;
    line-height: 30px;
    font-weight: bolder;
}
.TodoItemP-completado{
    text-decoration: line-through;
}
.Icon{
    height: 48px;
    width: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: bold;
}
.IconCheck{
    position: absolute;
    left: -1px;
}
.IconCheckActive{
    color: rgb(0, 189, 16);
}
.IconDelet{
    font-size: 25px;
    position: absolute;
    top: -24px;
    right: 0;
}
.IconDelet:hover{
    color: rgb(228, 27, 0);
}