form{
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    color: #000;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 3px;
}
label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 26px;
}
textarea{
    resize: vertical;
    background-color: #fff;
    border: 1px solid #000;
    border-radius: 3px;
    color:rgb(0, 0, 0);
    font-size: 20px;
    text-align: center;
    height: 150px;
    width: calc(100% - 15px);
    padding: 12px;
}
textarea::placeholder{
    color: rgba(65, 65, 65, 0.561);
    font-family: 'Montserrat Alternates', sans-serif;
}
textarea:focus{
    outline-color: rgb(3, 148, 245);
}
.ButtonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}
.Button{
    cursor:pointer;
    display: inline-block;
    font-size: 20px;
    color: rgb(0, 0, 0);
    font-weight: bold;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat Alternates', sans-serif;
}
.ButtonCancel{
    background:rgba(245, 3, 3, 0.719)
}
.ButtonAdd{
    background:rgb(3, 148, 245);
}