.CreateTodoButton{
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: rgb(48, 131, 187);
    font-size: 90px;
    position: fixed;
    bottom: 48px;
    right: 48px;
    font-weight: bolder;
    text-shadow: 0px 0px 8px rgb(230, 227, 28);
    transform: rotate(0);
    transition: all 0.1s linear;
    z-index: 1;
}
.CreateTodoButton:hover{
    transform: rotate(45deg);
}